@import url(https://fonts.googleapis.com/css2?family=Alfa+Slab+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.border {
  box-shadow: inset 0 0 40px #D8C3A5;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

/*
//
//
//
// HEADER
*/
.special-font {
  font-family: 'Alfa Slab One';
}

.navActive {
  color: #D8C3A5;
}

.title:hover {
  cursor: pointer;
}

ul {
  list-style-type: none;
}

li:hover {
  cursor: pointer;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  color: black;
  text-decoration: none;
}

a:hover {
  cursor: pointer;
}

.hero {
  background-image: url(/all-day-construction/static/media/downtown_phoenix.b0d89ae9.jpg);
  background-size: cover;
  background-position: bottom;
  background-attachment: scroll;
  background-position: 80%;
  height: 50vh;
}

header {
  background-color: #E85A4Fcc;
}

.header-text {
  max-width: 1300px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/*
//
//
//
// MAIN SECTION
*/

.content {
  background-color: white;
  margin: -30px auto 0px auto;
  max-width: 1300px;
  padding: 2em;
}

.content-title {
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  font-family: 'Alfa Slab One';
  color: #E85A4F;
}

.about-section {
  float: right;
  display: block;
}

.about-image-holder {
  display: table;
  height: 200px;
  width: auto;
  position: relative;
  float: right;
  clear: right;
  margin-left: 20px;
}

.about-image-holder-left {
  display: table;
  height: 200px;
  width: auto;
  position: relative;
  float: left;
  clear: left;
  margin-right: 20px;
}

.about-text {
  display: block;
  text-align: justify;
}

.about-image {
  height: 200px;
  width: auto;
}

.about-part {
  margin-bottom: 2em;
}

.image-stack {
  position: relative;
}

.image-stack:hover .overlay {
  bottom: 0;
  height: 100%;
}

.overlay {
  position: absolute;
  bottom: 100%;
  left: 0;
  right: 0;
  background-color: #D8C3A5cc;
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: .5s ease;
}

.project-photo {
  width: 320px;
  height: 230px;
}

form {
  width: 80%;
}

.overlay-text {
  white-space: nowrap; 
  font-size: 20px;
  position: absolute;
  overflow: hidden;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
}






/*
//
//
//
// Footer
*/

.footer {
  background-color: #D8C3A5;
  padding: 10px;
}

.logo {
  height: 75px;
  width: auto;
}

@media screen and (max-width: 1200px) {
  .content-title {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

